

*{
    margin:0;
    padding:0;
}
li{
    list-style: none;
}
.div-a{
    width: 100%;
    height: 0.7rem;
   display: flex;
   justify-content: flex-start;
   align-items: center;
   background: red;
}
.div-a .span1{
    margin-left: 0.25rem;
}
.div-a .span2{
    margin-left: 38%;
}
.div-b img{
	width: 100%;
}
.div-b p{
    font-size: 0.3rem;
}
.div-b .p1{
    color:black;
}
.div-b .p2{
    color:red;
}
.div12{
    position: relative;
    width: 100%;
    height: 0.8rem;
  
    top:2.2rem;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
}
.div12 button{
    border:0;

	float: right;
	height: 100%;
    background: orangered;
    width: 50%;
    
}
.div12 .btn1{
    background: #ccc;
}
.div13{
    font-size: 0.4rem;
    background: yellow;
    position: absolute;;
    bottom: 0rem;
    z-index: 2;
    width: 100%;
    height: 3rem;
    
}
.div13-3{
    height: 1rem;
   
    position: absolute;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 1.3rem;
}

.beijing{
    display: none;
    width:1.6rem;
    height: 1.6rem;
    overflow: hidden;
    position: fixed;
    top: 3rem;
    left: 34%;
}
.beijing img{
    display: block;
    width: 1.6rem;
    height: 1.6rem;
    border-radius: 0.3rem;
}