*{
    margin:0;
    padding:0;
}
li{
    list-style: none;
}
.div1{
   
    height: 1.4rem;
    font-size: 0.25rem;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    border-bottom: 0.01rem solid #ccc;
}
.div1 .div1-1{
    width: 1.05rem;
    height: 1.05rem;
    overflow: hidden;
    
    border-radius: 50%;
    margin-left: 0.15rem;
}
.div1 .div1-1 img{
    display: block;
    width: 1.1rem;
    height: 1.1rem;
}
.div2 .fon2{
    font-size: 0.4rem;
}
.span1{
    margin-left: 0.15rem;
    
}

.div2{
    width: 100%;
    height: 1rem;
    display:flex;
    padding:0.3rem 0;
    justify-content: space-around;
    border-bottom: 0.1rem solid #ccc;
}
.div2 figure{
    margin:0;
    padding:0;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
 
}

.div3{
    width:100%;
    padding-left: 0.16rem;
}
.div3 figure{
    display:flex;
    align-items: center;
    justify-content: flex-start;
    margin:0;
    padding:0;
    height: 0.6rem;
    border-bottom: 0.01rem solid #ccc;
    margin-top: 0.2rem;
}
.div3 figure span{
    margin-left: 0.16rem;
}
.teshu{
    margin-left: 1.5rem;
    font-size: 0.3rem;
}
.teshu p{
	font-size: 0.28rem;
	margin-top: 0.17rem;
	color: deepskyblue;
}
