.title{
	height: 100vh;
	overflow: auto;
   
}
.title-a{
	margin-top: 50px;
	
}

.heard-a{
	display: flex;
	justify-content: space-around;
	align-items: center;
	width: 100%;
	height: 44px;
	position: fixed;
	z-index:2;
	
	background: #ccc;
	
}
.heard-a span{
	position: fixed;
	left:20px ;
	color: #CCCCCC;
	font-size: 20px;
}
.heard-a input{
	padding-left: 40px;
	border-radius: 15px;
	width: 86%;
	height: 72%;
	border: none;
	touch-action: none;
	background: #FFFFFF;
}

.title>header{
	height: 44px;
	width: 100%;
}

.ul1{
	display: flex;
	justify-content: space-around;
	flex-wrap: wrap;
	padding: 0 0.1rem;
	margin-top: 0.9rem;

}
.ul1-1{
	display: flex;
	flex-direction: column;
	width: 2.8rem;
	height: 4rem;
	margin-top: 0.2rem;

}
.ul1-1 img{
	display:block;
	width: 2.8rem;
	height: 3rem;
}
.ul1-1 .p1{
	color: #ccc;
}
.ul1-1 .p2{
	color: red;
}
.ul1-2 p{
	 margin:0.05rem;
}
.ul1-2{
	display: flex;
	flex-direction: column;
	flex: 1;
	justify-content: center;
	align-items: flex-start;
}