.header-c{
    height: 44px;
	color: black;
	
    background: #ccc;
    text-align: center;
    line-height: 44px;
}
.del{
	width: 20px;
}
.am-list-item .am-list-line{
	flex-direction: column;
}
.am-list-item .am-list-line .am-list-extra{
	height:59px;
}
.cart{
	display: flex;
	flex-direction: column;
}
.cart section{
	flex: 1;
}
.cartBox{
	margin-top:10px;
	overflow: auto;
}
.cart .jies{
	width:100vw;
	height:65px;
	border-top:1px solid #ccc;
	border-bottom:1px solid #ccc;
}
.cart .jies div{
	float: left;
	width:67%;
	height:65px;
	margin-left:10px;
}
.cart .jies div p:first-child{
	height:32px;
	line-height:32px;
	color:red;
	font-size:18px;
}
.xuan{
	color:red;
	font-size: 18px;
}
.cart .jies div p:last-child{
	color:#676767;
	font-size:12px;
}
.cart .jies button{
	float: right;
	background:#4d5868;
	color:#fff;
	width:30%;
	height:65px;
	line-height:65px;
	border:0;
}

.xqbox{
	width:100vw;
	background:#fff;
}
.xqbox li{
	width:100vw;
	height:155px;
	border-bottom:1px dashed #ccc;
}
.xqbox li img{
	width:70px;
	height:90px;
	margin:10px 20px;
}
.xqbox li .num{
	float: right;
	width:60%;
	margin:10px 20px;
}
.xqbox li .num p:nth-child(1){
	float: left;
	width:159px;
	height:20px;
	line-height:20px;
	overflow: hidden;
	white-space: nowrap;
	text-overflow: ellipsis;
	font-size:16px;
	color: #666666;
}
.xqbox li .num p:nth-child(2){
	float: left;
	width:100%;
	height:38px;
	line-height:38px;
	color:#ccc;
	
}
.xqbox li .num p:nth-child(4) div{
	float:right;
	font-size:30px;
}
.xqbox li .num p:nth-child(4){
	float: right;
	width:30%;
	height:30px;
}
.xqbox li .num p:nth-child(3){
	float: left;
	width:58%;
}