*{
    margin:0;
    padding:0;
}
li{
    list-style: none;
}
.div5{
    width: 100%;
    height: 0.7rem;
   display: flex;
   justify-content: flex-start;
   align-items: center;
   background: red;
}
.div5 .span1{

    margin-left: 0.25rem;
}
.div5 .span2{
    margin-left: 38%;
}
.header2{
    height: 1.9rem;
    display: flex;
    justify-content: center;
    align-items: center;
}
.header2 img{
    display: block;
    width: 1.3rem;
    height: 1.3rem;
}
.div6{
    padding: 0 0.7rem;
    height: 100%;
    
    overflow: hidden;
    
}
.div6-1{
	display: flex;
	justify-content: space-between;
}
.div6 .div1-1{
    width: 100%;
    height: 0.5rem;
    positions: relative;
    display:flex;
    align-items: center;
    justify-content: flex-start;
    margin-top: 0.24rem;
}
.div6 .div1-1 span{
    font-size: 0.45rem;
}
.div6 .div1-1 input{
    width: 4.38rem;
    height: 100%;
    border:0.01rem solid #ccc;
    margin-left: 0.22rem;
   
}
.div6 .div1-1 .tap{
    
    position: absolute;
    right:0.8rem;
}
.div6-b{
    
    width: 100%;
    height:0.66rem;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 0.22rem;
    color: #fff;
    margin-top: 0.36rem;
    background: #d1d1d1;
    border-radius: 0.1rem;
    border:0;
}
.div6-c{
    
    width: 100%;
    height:0.66rem;
    display: flex;
    /* display: none; */
    justify-content: center;
    align-items: flex-end;
    font-size: 0.27rem;
    color: red;
    
    border-radius: 0.1rem;
    border:0;
}
.div6 .div6-1{
    height: 0.8rem;
    display:flex;
    justify-content: space-between !important;
    align-items: center;
}
.div6 .div6-2{
    height: 1.1rem;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 900;
}
.div6 .div6-3{
    height: 1.4rem;
    display: flex;
    justify-content: space-around;
}
.div6 figure{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin:0;
    padding:0;
}
.div6 figure .fon1{
    color:#49a6ff;
    font-size: 0.78rem;
}
.div6 figure .fon2{
    color:#0ba513;
    font-size: 0.78rem;
}
.div6 figure .fon3{
    color:#e73736;
    font-size: 0.78rem;
}
footer{
	display: none ;
}
